import React, {useState} from 'react'
import {connect} from 'react-redux'
import LayoutPermission from "components/permission-components/layout.permission";
import ButtonPermission from "components/permission-components/button.permission";
import HeroComponents from "../../../../../components/layout-components/CustomrPageHeaderAlt";
import {Button, Card, Col, Form, Input, message, Row, Select} from "antd";
import CustomTab from "../../../../../components/layout-components/custom.tab";
import ContainerTailwind from "../../../../../components/layout-components/ContainerTailwind";
import MdLocationSelect from "../../../components/forms/md-location.select";
import PreloaderText from "../../../../../components/animated-components/preloader-text";
import {SaveOutlined} from "@ant-design/icons";
import ApiService from "../../../../../services/api.service";
import ThinClientStatusSelect from "../../../../../components/forms-component/thin-client-status.select";
import {useHistory} from "react-router-dom";
import AirlineTypeSelect from "../../../../../components/forms-component/airline-type.select";
import {findKey, SearchLikeArr} from "../../../../../utils";
import TypeStatusAirlinesForm from "../../../components/forms/type-status-airlines.form";

function CreatePages() {
    const history = useHistory()
    const [form] = Form.useForm()
    const [loading, setLoading]= useState(false)
    function onFinish(){
        message.loading({
            key:"create-thin-client",
            content:<PreloaderText text={'Loading'} dots={3} speed={300}/>,
            duration:5
        })
        setLoading(true)
        form.validateFields()
            .then((value)=> {
                new ApiService({
                    url:`/api/v1/thin-client`,
                    body:value
                })
                    .post()
                    .then((response)=> {
                        if(response?.success){
                            message.success({
                                key:"create-thin-client",
                                content:response?.message ?? "Some Error Created",
                                duration:5
                            })
                            setTimeout(()=> {
                                history.replace('/dashboard/master-data/thin-client')
                            },1000)
                        }else{
                            message.error({
                                key:"create-thin-client",
                                content:response?.message ?? "Some Error Created",
                                duration:5
                            })
                        }
                        setLoading(false)
                    })
                    .catch((err)=> {
                        message.error({
                            key:"create-thin-client",
                            content:err?.message ?? "Some Error Created",
                            duration:5
                        })
                        setLoading(false)
                    })
            })
            .catch((err)=> {
                message.error({
                    key:"create-thin-client",
                    content:err?.message ?? "Some Error Created",
                    duration:5
                })
                setLoading(false)
            })
    }
    
    console.log(window.origin,'ORIGIN')
    return (

        <React.Fragment>
            <HeroComponents
                loading={false}
                name={"Create"}
                positionName={['Master Data','Thin Client']}
                thumbnail={null}
                extra={<>
                    <ButtonPermission
                        permission={['admin']}
                        theme={{
                            type: "notification",
                            property: {
                                message: "Access Denied",
                                description: "Anda tidak dapat menyimpan ke database "
                            }
                        }}
                        config={{
                            icon: <SaveOutlined />,
                            type: "primary",
                            danger:false,
                            loading,
                            ghost:true,
                            size: "small",
                            onClick: function () {
                                onFinish();
                            }
                        }}>
                        <span>Save</span>
                    </ButtonPermission>
                </>}
            />

            <ContainerTailwind>
                <CustomTab
                    defaultTab={'general'}
                    tab={[
                        {
                            key: 'general',
                            title: 'General',
                            render: function () {
                                return (
                                    <React.Fragment>

                                        <Form
                                            layout={'vertical'}
                                            form={form}
                                        >
                                            <Row gutter={24}>
                                                <Col xs={24} lg={16}>
                                                    <Card bordered={false}>
                                                        <Row gutter={24}>
                                                            <Col xs={24} lg={12}>
                                                                <Form.Item
                                                                  hasFeedback
                                                                  name={'ip_address'} label={'IP Address'}
                                                                  rules={[{required: true, message: "Can't be empty!"}]}>
                                                                    <Input placeholder={'Enter a IP Address'}                                                                         autoComplete={'off'}
                                                                           autoSave={'false'}/>
                                                                </Form.Item>
                                                            </Col>
                                                            <Col xs={24} lg={12}>
                                                                <Form.Item
                                                                    hasFeedback
                                                                    label={'Display Name'}
                                                                    name={'display_name'}
                                                                    rules={[{required: true, message: "Can't be empty!"}]}
                                                                >
                                                                    <Input
                                                                        placeholder={'Enter a name to display in several tables '}
                                                                        autoComplete={'off'}
                                                                        autoSave={'false'}
                                                                    />
                                                                </Form.Item>
                                                            </Col>
                                                        </Row>
                                                        <Form.Item
                                                            hasFeedback
                                                            name={"remarks"} label={'Remarks'}
                                                        >
                                                            <Input.TextArea
                                                                placeholder={'Enter a remarks ( optional ) '}
                                                                allowClear
                                                                showCount={true}
                                                                autoSize={{ minRows: 4, maxRows: 6 }}
                                                                size={"small"}
                                                            />
                                                        </Form.Item>

                                                    </Card>
                                                </Col>
                                                <Col xs={24} lg={8}>
                                                    <Card bordered={false}>
                                                        
                                                        <ThinClientStatusSelect
                                                          selectKey={['id']}
                                                          selectLabel={['data','name']}
                                                          selectValue={['data','name']}
                                                          name={'status'} label={'Status'} select={{showSearch:true}}/>
                                                        
                                                        <TypeStatusAirlinesForm
                                                          name={'location'}
                                                          label={'Location'}
                                                          keyValue={['data','identifier']}
                                                          keyDisplay={['data','name']}
                                                          select={{
                                                              showSearch:true,
                                                              placeholder:"Select your type"
                                                          }}
                                                          mapping={function({data}){
                                                              return (
                                                                data.map((item,index)=> (
                                                                  <Select.Option value={item?.identifier} key={item?.id}>{findKey(item,['data','name']) ?? "undefined"}</Select.Option>
                                                                ))
                                                              )
                                                          }}
                                                          rules={[
                                                              {
                                                                  required:true,
                                                                  message: "Can't be empty!"
                                                              }
                                                          ]}
                                                        />
                                                    </Card>
                                                </Col>
                                            </Row>
                                        </Form>
                                    </React.Fragment>
                                )
                            }
                    },
                    ]}
                    />
            </ContainerTailwind>
        </React.Fragment>
    )
}

    CreatePages.propTypes = {}
    CreatePages.defaultProps = {}

    export default connect(() => {
        return {}
    }, {})(React.memo(CreatePages))